body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  touch-action: none
}
.show-info {
  margin-top: 18px;
  display: flex;
  align-items: center;
}
.show-info .progress {
  margin-right: 5px;
  width: 100%;
}

.customizedButton-Blue{
  background-color:#108ee9;
  border:0;
  border-radius:5px;
  width:100%;
  height:47px; 
  color:white;
  font-size:18px
}

.customizedButton-White{
  background-color:white;
  border-radius:5px;
  width:100%;
  height:47px; 
  color:#108ee9;
  font-size:18px;
  border-style:solid;
  border-width:1px;
  box-sizing:border-box;
}

a {
  text-decoration: none
}
